import React from 'react';

import { Container, makeStyles, Chip } from '@material-ui/core';
import useNavigation from 'src/lib/hooks/useNavigation';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
    paddingTop: theme.spacing(1),
  },
  chipRoot: {
    minWidth: '15vw',
    fontSize: '0.7em',
    maxWidth: 150,
    margin: `0 1vw 4px 0`,
  },
}));

type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type KuralRouteChipProps = {
  items: KuralChipItem[];
};

const KuralRouteChip: React.FC<KuralRouteChipProps> = (
  props: KuralRouteChipProps
) => {
  const classes = useStyles(props);
  const [navigateTo] = useNavigation();
  return (
    <Container classes={{ root: classes.root }}>
      {props.items.map((item, index) => (
        <Chip
          size='small'
          label={item.label}
          onClick={navigateTo(item.navRoute)}
          color={item.color}
          classes={{ root: classes.chipRoot }}
          key={index}
        />
      ))}
    </Container>
  );
};

export default KuralRouteChip;
