import React from 'react';

import {
  getKuralsOfChapter,
  Kural,
  getChapterInfo,
  ChapterInfo,
} from './../../lib/api/kural';

type UseChapterType = (
  chapterNo: number
) => [Kural[], () => void, () => void, ChapterInfo];

const useChapter: UseChapterType = (chapterNo = 1) => {
  const [chapter, setChapter] = React.useState(chapterNo);
  const kurals = getKuralsOfChapter(chapter);
  const chapterInfo = getChapterInfo(chapter);
  const prevChapter = (): void => {
    if (chapter === 1) {
      setChapter(133);
    } else {
      setChapter(chapter - 1);
    }
  };
  const nextChapter = (): void => {
    if (chapter === 133) {
      setChapter(1);
    } else {
      setChapter(chapter + 1);
    }
  };
  return [kurals, prevChapter, nextChapter, chapterInfo];
};

export default useChapter;
