import React from 'react';

import { Container, makeStyles, Card, CardMedia } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    background: 'rgb(232 199 24 / 78%)',
  },
  media: {
    paddingTop: '100%',
    height: 0,
  },
}));

type MyImageCardProps = {
  imagePath: string;
};

const MyImageCard: React.FC<MyImageCardProps> = (props: MyImageCardProps) => {
  const classes = useStyles();
  return (
    <Container classes={{ root: classes.root }}>
      <Card>
        <CardMedia
          image={props.imagePath}
          title='thirukural'
          className={classes.media}></CardMedia>
      </Card>
    </Container>
  );
};

export default MyImageCard;
