import React from 'react';

type DrawerState = {
  isOpened: boolean;
};

type useDrawerType = (
  state?: DrawerState
) => [DrawerState, (status?: boolean) => void];

const defaultDrawerState: DrawerState = {
  isOpened: false,
};
const useDrawer: useDrawerType = (state = defaultDrawerState) => {
  const [dState, setDState] = React.useState<DrawerState>(state);
  const toggleDrawer = (status = !dState.isOpened): void => {
    setDState({
      ...dState,
      isOpened: status,
    });
  };
  return [dState, toggleDrawer];
};

export default useDrawer;
