import React from 'react';

import { makeStyles } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import SwipeableDrawer, {
  SwipeableDrawerProps,
} from '@material-ui/core/SwipeableDrawer';
import Image from 'src/assets/images/thirukural.png';

import MyImageCard from './MyImageCard';

type MyDrawerProps = {
  drawerProps: SwipeableDrawerProps;
  toggleDrawer: (status?: boolean) => void;
};

const useStyles = makeStyles((theme) => ({
  paper: {
    width: '60%',
  },
}));
const MyDrawer: React.FC<MyDrawerProps> = (props: MyDrawerProps) => {
  const classes = useStyles();
  return (
    <div>
      <SwipeableDrawer
        anchor='left'
        {...props.drawerProps}
        classes={{ paper: classes.paper }}>
        <MyImageCard imagePath={Image} />
        {list(props.toggleDrawer)}
      </SwipeableDrawer>
    </div>
  );
};

const list = (toggleDrawer: (staus?: boolean) => void) => (
  <div
    role='presentation'
    onClick={(): void => {
      toggleDrawer();
    }}>
    <List>
      {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
        <ListItem button key={text}>
          <ListItemIcon>{index % 2 === 0 ? 'ac' : 'dda'}</ListItemIcon>
          <ListItemText primary={text} />
        </ListItem>
      ))}
    </List>
    <Divider />
    <List>
      {['All mail', 'Trash', 'Spam'].map((text, index) => (
        <ListItem button key={text}>
          <ListItemIcon>{index % 2 === 0 ? 'ac' : 'dda'}</ListItemIcon>
          <ListItemText primary={text} />
        </ListItem>
      ))}
    </List>
  </div>
);

export default MyDrawer;
