/* eslint-disable @typescript-eslint/no-non-null-assertion */

import {
  KURALS_MAP,
  SECTION_MAP,
  CHAPTER_MAP,
  LITERATURE_MAP,
} from '../../data';

interface Kural {
  s: number;
  l: number;
  c: number;
  k: string;
}

interface ChapterInfo {
  section: string;
  literature: string;
  chapter: string;
}
const getKuralsOfChapter = (chapterNo: number): Kural[] => {
  if (chapterNo >= 1 && chapterNo <= 133) {
    const MaxKural = chapterNo * 10;
    return Array.from(Array(10).keys())
      .map((i) => MaxKural - i)
      .sort((a, b) => a - b)
      .map((kId) => KURALS_MAP[kId]);
  }
  return [];
};

const getChapterInfo = (chapterNo: number): ChapterInfo => {
  const sectionId = Object.keys(SECTION_MAP).find((sectionId) => {
    const [from, to] = SECTION_MAP[sectionId].chapIndex;
    return chapterNo <= from || chapterNo >= to;
  })!;

  const litChapMap = SECTION_MAP[sectionId].litChapMap;
  const litId = Object.keys(litChapMap).find((litId) => {
    const [from, to] = litChapMap[litId];
    return chapterNo <= from || chapterNo >= to;
  })!;
  return {
    section: SECTION_MAP[sectionId].title,
    literature: LITERATURE_MAP[litId],
    chapter: CHAPTER_MAP[chapterNo.toString()],
  };
};

export { getKuralsOfChapter, getChapterInfo };
export type { Kural, ChapterInfo };
