import React from 'react';

import { Paper, Fab, makeStyles } from '@material-ui/core';
import List from '@material-ui/core/List';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import clsx from 'clsx';
import { Swipeable, EventData } from 'react-swipeable';

import useChapter from './../../../lib/hooks/useChapter';
import KuralCard from './KuralCard';
import KuralRouteChip from './KuralRouteChip';

const useStyles = makeStyles((theme) => ({
  fav: {
    position: 'absolute',
    top: '50%',
    zIndex: 2000,
    background: 'transparent',
    // TODO;
  },
  leftFav: {
    left: 0,
  },
  rightFav: {
    right: 0,
  },
}));

type KuralListByChapterProps = {
  chapter: number;
};

const KuralListByChapter: React.FC<KuralListByChapterProps> = (
  props: KuralListByChapterProps
) => {
  const classes = useStyles();
  const [kurals, prevChapter, nextChapter, chapterInfo] = useChapter(
    props.chapter
  );
  const swipeHandler = (event: EventData | { dir: 'Right' | 'Left' }): void => {
    if (event.dir === 'Right') {
      prevChapter();
    } else if (event.dir === 'Left') {
      nextChapter();
    }
  };

  return (
    <>
      <KuralRouteChip
        items={[
          { label: chapterInfo.section, navRoute: '/abc', color: 'primary' },
          {
            label: chapterInfo.literature,
            navRoute: '/abc2',
            color: 'secondary',
          },
          { label: chapterInfo.chapter, navRoute: '/abc', color: 'primary' },
        ]}
      />
      <Swipeable onSwiped={swipeHandler}>
        <Paper elevation={2}>
          <List>
            {kurals.map((k, i) => (
              <KuralCard key={i} kural={k.k} />
            ))}
          </List>
        </Paper>
      </Swipeable>
      <Fab
        color='secondary'
        aria-label='add'
        className={clsx(classes.fav, classes.leftFav)}
        onClick={(): void => swipeHandler({ dir: 'Left' })}>
        <ArrowBackIcon />
      </Fab>
      <Fab
        color='secondary'
        aria-label='add'
        className={clsx(classes.fav, classes.rightFav)}
        onClick={(): void => swipeHandler({ dir: 'Right' })}>
        <ArrowForwardIcon />
      </Fab>
    </>
  );
};

export default KuralListByChapter;
