type DummyAction = 'add';

const reducer = (
  data = [1, 2, 3],
  action: { type: DummyAction; payload: number }
): number[] => {
  if (action.type === 'add') {
    return [...data, action.payload];
  }
  return data;
};

const AddItem = (item: number) => {
  return {
    type: 'add',
    payload: item,
  };
};

export { AddItem, reducer };
