import React from 'react';

import FavoriteIcon from '@material-ui/icons/Favorite';
import HomeIcon from '@material-ui/icons/Home';
import SearchIcon from '@material-ui/icons/Search';
import SettingsIcon from '@material-ui/icons/Settings';
import { renderRoutes } from 'react-router-config';

import MyActionBar from '../../components/common/MyActionBar';
import MyAppBar from '../../components/common/MyAppBar';
import MyDrawer from '../../components/common/MyDrawer';
import useDrawer from './../../../lib/hooks/useDrawer';

const ActionBarItems = [
  {
    icon: <HomeIcon />,
    route: '/',
  },
  {
    icon: <SearchIcon />,
    route: '/search',
  },
  {
    icon: <FavoriteIcon />,
    route: '/favorite',
  },
  {
    icon: <SettingsIcon />,
    route: '/settings',
  },
];

const Main: React.FC<any> = (props: any) => {
  const [drawerState, toggleDrawer] = useDrawer();
  return (
    <div className='container'>
      <MyAppBar drawerToggle={toggleDrawer} title='Thirukural' />
      <MyDrawer
        toggleDrawer={toggleDrawer}
        drawerProps={{
          open: drawerState.isOpened,
          swipeAreaWidth: 20,
          onOpen: (): void => {
            toggleDrawer();
          },
          onClose: (): void => {
            toggleDrawer(false);
          },
        }}
      />
      {renderRoutes(props.route.routes)}
      <MyActionBar items={ActionBarItems} />
    </div>
  );
};

export default Main;
